.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* styles.css or your relevant CSS file */
.nav {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove default padding */
  margin: 0; /* Remove default margin */
  border: 1px solid #ccc; /* Add a border around the nav */
  border-radius: 5px; /* Round the corners */
  display: inline-block; /* Ensure the box wraps around the items */
  background-color: #f9f9f9; /* Background color */
}

.nav-item {
  display: inline-block; /* Ensure items are inline and allow padding/margins */
  margin-right: 15px; /* Space between items */
}

.nav-link {
  text-decoration: none; /* Remove underline */
  padding: 10px 15px; /* Add padding (top/bottom, left/right) */
  color: black; /* Change text color */
  display: block; /* Make the link a block to fill the item */
}

.nav-link.active {
  font-weight: bold; /* Style for active link */
  color: blue; /* Active link color */
}

/* Hover effect */
.nav-link:hover {
  background-color: #e0e0e0; /* Change background on hover */
  border-radius: 5px; /* Round corners on hover */
}
